                              mt-n4
                              white--text
                              font-weight-black
                              ml-9
                              mb-n2
                            "
                            >{{ "Energy E0" }}</v-list-item-title
                          >
                        </v-col>
                      </v-row>
                      <v-row class="truncate">
                      <v-col>
                        <v-list-item-title
                          class="
                            Subtitle
                            2
                            mt-n3
                            white--text
                            font-weight-black
                            ml-n1
                          "
                          ><span>{{"(พลังงานที่ใช้) (kWh)" }}</span></v-list-item-title
                        >
                      </v-col>
                    </v-row>
                    </v-container>
                  </v-card>
                </v-col>
                <v-col cols="2">
                  <v-card
                    color="amber"
                    style="border-radius: 10px; height: 65px; width: 212px"
                    class="mb-3 mt-3 ml-9"
                  >
                    <v-container>
                      <v-row class="truncate">
                        <v-col>
                          <v-list-item-title
                            class="
                              Subtitle
                              2
                              mt-n4
                              white--text
                              font-weight-black
                              ml-9
                              mb-n2
                            "
                            >{{ "Energy E1" }}</v-list-item-title
                          >
                        </v-col>
                      </v-row>
                      <v-row class="truncate">
                      <v-col>
                        <v-list-item-title
                          class="
                            Subtitle
                            2
